@use "base/mixins" as m;
@use "base/variables" as v;

.table {
     width: 100%;
     border-collapse: collapse;
     &__thead {
          background-color: v.$gris-oscuro;
          color: v.$blanco;
     }
     &__th {
          padding: 1rem;
          text-align: left;
     }
     &__tr {
          &:nth-child(even) {
               background-color: v.$gris-claro;
          }
     }
     &__td {
          padding: 1.5rem;
          &--acciones {
               @extend .table__td;
               display: flex;
               justify-content: flex-end;
               align-items: center;
               gap: 2rem;
          }
     }
     &__accion {
          all: unset; //elimina los estilos
          text-transform: uppercase;
          font-size: 1.5rem;
          font-weight: v.$bold;
          &--editar {
               color: v.$azul;
          }
          &--eliminar {
               color: v.$rojo;
          }
          &:hover {
               cursor: pointer;
          }
     }
}
