@use "base/mixins" as m;
@use "base/variables" as v;

.eventos-registro {
     @include m.contenedor;
     @include m.tablet {
          display: flex;
          align-items: flex-start; // se alinea hasta arriba
          gap: 2rem;
     }
     &__listado {
          @include m.tablet {
               flex: 0 0 60%;
          }
     }
     &__grid {
          @include m.grid(1, 3rem);
          @include m.tablet {
               @include m.grid(2, 3rem);
               align-items: flex-start;
          }
     }
     &__heading {
          &--conferencias,
          &--workshops {
               white-space: nowrap;
               @include m.texto-fluido(2rem, 5rem);
          }
          &--conferencias {
               color: v.$azul;
          }
          &--workshops {
               color: v.$secundario;
               margin-top: 5rem;
          }
     }
     &__fecha {
          margin: 6rem 0 3rem 0;
     }
}
.registro {
     background-color: v.$gris-claro;
     padding: 3rem;
     border-radius: 2rem;
     @include m.tablet {
          flex: 0 0 40%;
          position: -webkit-sticky; //hace que este bloque se quede statico aun cuando se da scroll hacia abajo
          position: sticky;
          top: 3rem;
     }
     &__heading {
          margin: 0 0 3rem 0;
     }
     &__evento {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 1.5rem;
          &:nth-child(even) {
               background-color: darken(v.$gris-claro, 6);
          }
     }
     &__nombre {
          font-size: 2rem;
          font-weight: v.$bold;
          margin: 1rem 0;
     }
     &__eliminar {
          all: unset;
          color: v.$rojo;
          &:hover {
               cursor: pointer;
          }
     }
     &__texto{
          text-align: center;
          
     }

     &__regalo {
          margin-top: 3rem;
          padding-top: 2rem;
          border-top: 1px solid v.$azul;
     }
     &__label {
          display: block;
          font-size: 2.2rem;
          font-weight: v.$bold;
          margin-bottom: 1rem;
     }
     &__select {
          width: 100%;
          background-color: v.$blanco;
          border: 1px solid v.$gris;
          padding: 1rem;
          border-radius: 1rem;
     }
}
